.MatchDetailCard {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 10px;
  margin-bottom: 20px;
  box-shadow: #222222 1px 1px 10px;
  border-radius: 0.2rem;

  .vs {
    font-style: italic;
  }

  .match-date,
  .match-venue,
  .match-result {
    margin-top: 10px;
  }

  .additional-detail {
    text-align: right;
    & > p {
      margin-bottom: 20px;
    }
  }
}
