@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: lightgray;
}

body {
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}

.App{padding:20px;text-align:left}.won-card{background-color:#4da375;background-color:#4da375}.lost-card{background-color:#a34d5d;background-color:#a34d5d}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px;box-shadow:#222222 1px 1px 10px;border-radius:0.2rem}.MatchDetailCard .vs{font-style:italic}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px;box-shadow:#222222 1px 1px 10px;border-radius:0.2rem}.MatchSmallCard .vs{font-style:italic}.MatchSmallCard .match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.team-name-section{grid-column:span 3;justify-self:start;align-self:end}.team-name{font-size:4rem;margin-bottom:20px}.win-loss-section{justify-self:center;padding:30px;text-align:center}.match-detail-card-section{grid-column:span 4}.match-small-card-section{grid-column:span 4}.more-link{align-self:center;justify-self:center;font-size:1.5rem}

.YearSelector{text-align:center;list-style-type:none}.YearSelector li{margin-bottom:10px;margin-top:10px}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px auto}

.TeamTile{height:170px;text-align:center;border:1px solid white;border:1px solid #c8d6e5;padding:20px;box-shadow:#222222 1px 1px 10px;border-radius:0.2rem}

.HomePage .app-name{font-size:3rem;margin:30px auto}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}

.NavBar{background-color:#044d5f;text-align:left;font-weight:bold;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:10px}

